<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="(text, index) in texts" :key="index">
        <div
          :class="'swiper-con' + index"
          style="
            width: 100vw;
            height: 100vh;
            display: flex;
            align-items: center;
            justify-content: center;
          "
        >
          <div class="slide-content">
            <div
              style="
                width: 100%;
                display: grid;
                text-align: center;
                justify-items: center;
              "
            >
              <span :class="'swiper-title' + index">{{ text.title }}</span>
              <p :class="'swiper-dec' + index + ' dec-margin'">
                {{ text.comment }}
              </p>
              <p :class="'swiper-dec' + index">
                {{ text.comment1 }}
              </p>
            </div>
          </div>
          <!-- <div class="swipe_btn">
            <div class="swiper-button-prev"></div>
            <div class="swiper-button-next"></div>
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { ref, onMounted } from "vue";
import SwiperCore, { Mousewheel, Navigation } from "swiper";
import "swiper/swiper-bundle.css";

import ScrollOut from "scroll-out";
import store from "@/store";

SwiperCore.use([Mousewheel, Navigation]);

export default {
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  methods: {},
  setup() {
    const swiperRef = ref(null);
    const texts = ref([
      {
        title: "OVERVIEW",
        comment: "Develop the present.",
      },
      {
        title: "VISION",

        comment: "고객의 편안하고 긍정적인 경험을 위해, 보다 더 나은 기술을",
        comment1:
          "개발하고 빠르고 간편하게 고객과 비즈니스가 닿을 수 있도록 노력하겠습니다.",
        comment2: "",
      },
    ]);

    onMounted(() => {
      ScrollOut({
        cssProps: {
          visibleY: true,
        },
        threshold: 0.2,
        offset: 1,
      });

      swiperRef.value = new SwiperCore(".swiper-container", {
        direction: "horizontal",
        loop: false,
        mousewheel: true,
        slidesPerView: 1,
        speed: 1000,
        // autoplay: {
        //   delay: 3000,
        // },

        navigation: {
          prevEl: ".swiper-button-prev",
          nextEl: ".swiper-button-next",
        },
      });

      // 원페이지 트리거
      const container0 = document.querySelector(".swiper-con0");
      const container1 = document.querySelector(".swiper-con1");

      container0.addEventListener("wheel", function (e) {
        e.preventDefault();
        if (store.getters.isScrolling) {
          return;
        }
        store.dispatch("setIsScrolling", true);
        if (e.deltaY < 0) {
          window.scrollTo({
            top: -window.innerHeight,
            behavior: "smooth",
          });
        }
        setTimeout(() => {
          store.dispatch("setIsScrolling", false); // 스크롤 종료시 isScrolling 값을 false로 변경
        }, 500); // 1초 후에 변경되도록 설정
      });

      container1.addEventListener("wheel", function (e) {
        if (store.getters.isScrolling) {
          return;
        }
        store.dispatch("setIsScrolling", true);
        if (e.deltaY > 0) {
          window.scrollTo({
            top: window.innerHeight + window.scrollY,
            behavior: "smooth",
          });
        }
        setTimeout(() => {
          store.dispatch("setIsScrolling", false); // 스크롤 종료시 isScrolling 값을 false로 변경
        }, 500); // 1초 후에 변경되도록 설정
      });
    });
    return {
      swiperRef,
      texts,
    };
  },
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 1080px) {
  .swiper-title0 {
    font-size: 1.25rem;
  }
  .swiper-dec0 {
    font-size: 5rem;
  }
  .swiper-title1 {
    font-size: 1.25rem;
  }
  .swiper-dec1 {
    font-size: 1.625rem;
  }
}
// 노트북
@media screen and (min-width: 760px) and (max-width: 1080px) {
  .swiper-title0 {
    font-size: 0.89rem;
  }
  .swiper-dec0 {
    font-size: 3rem;
  }
  .swiper-title1 {
    font-size: 0.89rem;
  }
  .swiper-dec1 {
    font-size: 1.25rem;
  }
}
// 태블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .swiper-title0 {
    font-size: 0.531rem;
  }
  .swiper-dec0 {
    font-size: 2.125rem;
  }
  .swiper-title1 {
    font-size: 0.625rem;
  }
  .swiper-dec1 {
    font-size: 1rem;
  }
}
// 휴대폰
@media screen and (min-width: 320px) and (max-width: 639px) {
  .swiper-title0 {
    font-size: 0.563rem;
  }
  .swiper-dec0 {
    font-size: 2.25rem;
  }
  .swiper-title1 {
    font-size: 0.725rem;
  }
  .swiper-dec1 {
    font-size: 1rem;
  }
}

.swiper-container {
  margin-top: 100vh;
  height: 100vh;
  transition: all 1s;
  background-color: white;
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
  background-color: #ffffff;
  transition: all 1s;
  width: 100vw;
}

.slide-content {
  text-align: center;
  overflow-x: hidden;
  width: 100%;
}

.swiper-title0 {
  font-family: "Pretendard-Regular";
  opacity: 1;
  // transform: translateY(20px);
  transition: opacity 2s ease-out, transform 1s ease-out;
  box-shadow: #0174f5 0px 2px 0px 0px;
}

.swiper-title1 {
  font-family: "Pretendard-Regular";
  opacity: 1;
  // transform: translateY(20px);
  transition: opacity 2s ease-out, transform 1s ease-out;
  box-shadow: #0174f5 0px 2px 0px 0px;
}

.dec-margin {
  margin-top: 28px;
}
.swiper-dec0 {
  font-family: "Pretendard-Regular";
  font-weight: bold;
  opacity: 1;
  // transform: translateY(20px);
  transition: opacity 5s ease-out, transform 1s ease-out;
  // transform: translateX(12vw);
}

.swiper-dec1 {
  font-family: "Pretendard-Regular";
  font-weight: bold;
  opacity: 1;
  // transform: translateY(20px);
  transition: opacity 5s ease-out, transform 1s ease-out;
  width: 1100px !important;
  // transform: translateX(-28vw);
}

