  }
}

// 휴대폰
@media screen and (min-width: 320px) and (max-width: 639px) {
  .footer_con {
    width: 85vw;
  }
  .btn {
    width: 100px;
    height: 20px;
    font-size: 0.425rem;
  }
  .footer_info {
