  }
  .footer_logo {
    width: 130px;
    height: 30px;
  }
}

/* 풋터 */

.footer {
  position: relative;
  width: 100vw;
  height: auto;
  background-color: #2d2d2d;
  box-shadow: 0px 0px 1px 0px grey;
}

.footer_con {
  margin: auto;
}

.logo_con {
  padding-top: 12px;
  margin-bottom: 8%;
}
.footer_logo {
  cursor: pointer;
}
.footer_info {
  color: #ffffff;
  font-family: "Pretendard-Regular";
  margin-top: 12px;
}
.contact_btn {
  display: grid;
  width: 100%;
  padding-top: 12px;
  justify-content: right;
}
.btn {
  border: solid 1px white;
  display: flex;
  align-items: center;
  padding: 12px;
  margin: 3px;
}
a {
  text-decoration: none;
  color: white;
  width: 100%;
  justify-content: space-between;
  display: flex;
}
</style>
